    const sortTickets = (attrName: string) => {
      let sortbool:boolean = true;
      (selectedRows.length>1 ?
      sortbool = selectedRows[0][attrName].toString().toLowerCase()>selectedRows[1][attrName].toString().toLowerCase()
      : sortbool = true)
      selectedRows.sort((a,b) => 
          (a[attrName] && b[attrName] && 
            a[attrName].toString().toLowerCase()>b[attrName].toString().toLowerCase()
            && sortbool) ?
            (1) : (-1)
      );
      setSelectedRows(selectedRows);
      console.log(selectedRows)
    }
    const renderCustomTableHeader = (columns: any, fixedColumns: any) => {
        let dockedColumnsCounter = 0;
            return (
              <>
              {columns.map((column, index) => {
                const matchedColumn = fixedColumns.filter(col => col === column.name)[0];
                let nextColumnIsFixed = true;
                if (matchedColumn && columns[index + 1]) {
                  nextColumnIsFixed = fixedColumns.filter(col => col ===  columns[index + 1].name)[0] ? true : false;
                }
                const style = {
                  left: matchedColumn ? dockedColumnsCounter * 125 : 0,
                  zIndex: matchedColumn ? 125 : 0,
                  minWidth: '20px',
                  maxWidth: '300px',
                  paddingRight : '0px',
                  paddingLeft : '10px',
                  borderRight: !nextColumnIsFixed ? '#9EA3AC 2px solid' : undefined
                };
                if (matchedColumn) {
                  dockedColumnsCounter++;
                }
                return (
                  <TableCell style={style}>
                    <span className='header-2'>{column.title}</span>
                    <div style={{float: 'right'}}>
                        <IconButton
                            icon={faSort}
                            onClick={() => {
                                sortTickets(column.name);
                                setRenderTable(true);
                                setLoading(false);
                            }} />
                    </div>
                    <InputField
                        value={filters[column.name]}
                        onChange={(value: string) => {
                            filters[column.name] = value;
                            setFilters(filters);
                            setRenderTable(true);
                            // this.setState(prevState => ({...prevState, [attrName]: value}));
                        }} />
                  </TableCell>
                );
              })}
              </>
            );
    }
