    const getFilteredTickets = (columns: any) => {
        let filteredTickets = selectedRows; 
        if(largeSearch!=='')
        {   
            if(topnav === 'signed_proposals')
            {
                filteredTickets = filteredTickets.filter(function(element){
                    return (element['files'].includes(largeSearch))})
            } else {
                filteredTickets = filteredTickets.filter(function(element){
                    return (element['files'].includes(largeSearch) || 
                    element['document_name'].includes(largeSearch))})
            }
        }
        columns.map(column => {
          filteredTickets = filteredTickets
            .filter(ticket => 
                filters[column.name] ? (
                    filters[column.name] === '' ||
                (ticket[column.name] && 
                  ticket[column.name].toString().toLowerCase().includes(filters[column.name].toLowerCase())
                )
              ) : true
            ) 
         });
    
        return filteredTickets;
      }
