
import React from 'react';
import {
	Grid, Typography,
} from '@material-ui/core';
import {
	BasicTextField,
	BasicSelect,
	GridHorizontalDivider,
} from 'Components/UI/';
import { clientInformationStyles } from '../../ClientProfilePage'
interface ClientInformation {
	invalidFields: string[];
	clientInfo?:any;
	setEditInfo?:(e) => void;
	setData:any;
	setSideNav?:(e)=>void;
}
