export const EditBusinessInformation = (props:ClientInformation) => {
	//const {clientInfo, setData } = props;
	const classes = clientInformationStyles();
	const handleTextFieldOnChange = (name: string) => (event: any) => { 
		const data = { ...props.clientInfo, [name]: event.target.value };
		console.log('pressed')
		console.log(JSON.stringify(data,null,2))
		if (props.setData) {
			props.setData(data); 
		}
	};
	return (
		<Grid container>
			
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="registeredCorporateName"
					label="Registered Corporate Name*"
					value={props.clientInfo.registered_name}
					onChange={handleTextFieldOnChange('registered_name')}
				/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="tradeBrandName"
					label="Trade / Brand Name*"
					value={props.clientInfo.brand_name}
					onChange={handleTextFieldOnChange('brand_name')}
				/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="corporateAccountNo"
					label="Corporate Account No."
					value={props.clientInfo.corporate_account_no}
					onChange={handleTextFieldOnChange('corporate_account_no')}
				/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="secRegistrationNo"
					label="SEC Registration No.*"
					value={props.clientInfo.sec_reg_no}
					onChange={handleTextFieldOnChange('sec_reg_no')}
				/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="corporateTin"
					label="Corporate TIN*"
					value={props.clientInfo.tin_no}
					onChange={handleTextFieldOnChange('tin_no')}
				/>
			</Grid>
			<Grid item xs={3} className={classes.editContainer}>
					<BasicSelect
						id="vatType"
						label="VAT Type*"
						menuItems={vatTypeList}
						value={props.clientInfo.vat_type}
						onChange={handleTextFieldOnChange('vat_type')}
						width='100%'
					/>
			</Grid>
			<Grid item xs={3} className={classes.editContainer}>
				<BasicTextField
					id="eVatNo"
					label="E-VAT No."
					value={props.clientInfo.evat_no}
					onChange={handleTextFieldOnChange('evat_no')}
				/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="nameDisplayedInCard"
					label="Name Displayed In Card*"
					value={props.clientInfo.name_on_card}
					onChange={handleTextFieldOnChange('name_on_card')}
				/>
			</Grid>
			<GridHorizontalDivider height="30px" />
			<Grid item xs={6} className={classes.editContainer}>
				<Typography>Authorized Signatory</Typography>
			</Grid>
			<GridHorizontalDivider height="30px" />
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="authname"
					label="Name*"
					value={props.clientInfo.authorized_signatory}
					onChange={handleTextFieldOnChange('authorized_signatory')}
				/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="authcontactno"
					label="Contact Number"
					value={props.clientInfo.authorized_signatory_contact_no}
					onChange={handleTextFieldOnChange('authorized_signatory_contact_no')}
				/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="authemailadd"
					label="Email Address"
					value={props.clientInfo.authorized_signatory_email_address}
					onChange={handleTextFieldOnChange('authorized_signatory_email_address')}
				/>
			</Grid>
			<Grid item xs={6} className={classes.editContainer}>
				<BasicTextField
					id="authdesignation"
					label="Designation"
					value={props.clientInfo.authorized_signatory_designation}
					onChange={handleTextFieldOnChange('authorized_signatory_designation')}
				/>
			</Grid>
		</Grid>
		);
};


const vatTypeList = [
	{
		name: 'VATable',
		value: 'vat',
	},
	{
		name: 'VAT Zero-Rated',
		value: 'zero',
	},
	{
		name: 'VAT Exempt',
		value: 'exempt',
	},
];